<template>
    <div>           <!--:rules-->      <!--ref-->
      <el-form :model="ruleForm"  label-position="top" status-icon :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm"
      style="margin-top: 50px">
        <el-form-item label="" prop="username"> <!-- proprulesusername-->
          <el-input type="text" v-model="ruleForm.username" autocomplete="off" style="width: 300px"
                    prefix-icon="el-icon-user" placeholder="" ></el-input>
        </el-form-item>
        <el-form-item label="" prop="pass" >
          <el-input type="password" v-model="ruleForm.pass" autocomplete="off" style="width: 300px"
                    prefix-icon="el-icon-lock" placeholder=""></el-input>
        </el-form-item>
        <el-form-item style="margin-top: 15px;">
          <el-button type="primary" @click="login('ruleForm')" size="medium"></el-button>
        </el-form-item>
        <el-form-item style="padding-top: 3px;">
          <el-button  @click="resetForm('ruleForm')" size="medium"></el-button>
        </el-form-item>
      </el-form>
      <el-dialog :visible.sync="verifyModal" :closable="false" footer-hide width="350px">
        <slide-verify  ref="slideblock"
                       @again="onAgain"
                       @success="onSuccess"
                       @fail="onFail"
                       :accuracy="accuracy"
                       :imgs="imgs"
                       slider-text=""
        ></slide-verify>
      </el-dialog>
    </div>
</template>

<script>
    import {mapMutations} from 'vuex';

    export default {
        data() {
            var validateUsername=(rule, value, callback) => {
                if (value === '') {
                    callback(new Error(''));
                }
                    callback();

            };

            var validatePass = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error(''));
                } else {
                    if (this.ruleForm.checkPass !== '') {
                        this.$refs.ruleForm.validateField('checkPass');
                    }
                    callback();
                }
            };
            return {
                ruleForm: {
                    username:'',
                    pass: '',
                },
                rules: {   //rules
                    username: [
                        { validator: validateUsername, trigger: 'blur' },
                        { min:3,max:10,message:'3-10',trigger: 'blur'}
                    ],
                    pass: [
                        { validator: validatePass, trigger: 'blur' },
                        { min:6,max:14,message:'6-14',trigger: 'blur'}
                    ],

                },
              verifyModal:false,
              // 15
              accuracy: 7,
              imgs:["http://q7u9zowxe.bkt.clouddn.com/guimie.jpg",
                "http://q7u9zowxe.bkt.clouddn.com/wlopbackground.jpg",
                "http://q7u9zowxe.bkt.clouddn.com/timg.jpg",
                "http://q7u9zowxe.bkt.clouddn.com/yourname.jpg"]
            }
        },
        created:function () {
            let that = this;
            document.onkeydown = function(e){

                let key = window.event.keyCode;

                if(key === 13 || key === 100){

                    that.login('ruleForm');

                }

            }
        },
        methods: {
            ...mapMutations(['setToken']),
          onSuccess(){
            let that=this;
            let url='/user/login';
            let user=this.$qs.stringify({
              username:this.ruleForm.username,
              password:this.ruleForm.pass
            });
            this.$axios.post(url,user
            ).then(res=>{
              console.log(res);
              if(res.data.token!=null&&res.data.token!==''){
                that.token=res.data.token;
                console.log(that.token);
                that.setToken({token:that.token});    //vuextoken
                //localStorage.setItem('token',that.token);
                console.log(res.data.user);
                localStorage.setItem("user",JSON.stringify(res.data.user));  //localStorage
                that.$router.push('/home');
                that.$notify.success('');
              }else {
                that.$notify.error('');
                that.verifyModal=false;
                this.$refs.slideblock.reset();
              }
            }).catch(err=>{
              this.$notify.error('');
              console.log(err);
            });
          },
          onFail(){
            this.$message.error("");
          },
          onAgain() {
            this.$message.warning("");
            this.$refs.slideblock.reset();
          },
            login(formName) {
                this.$refs[formName].validate((valid) => {   //validatetruefalse
                    if (valid) {
                      this.verifyModal=true;
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            },
            resetForm(formName) {
                this.$refs[formName].resetFields();
            }
        }
    }
</script>

<style lang="less" scoped>
.el-form{
  margin-left: 30px;
  .el-form-item{
    margin-top: -15px;
  }
}
  .el-button{
    width: 300px;
  }
</style>



